/**
 *  axios,  token
 *  401  /login 
 * 
 *  request  API 
 */
import axios from "axios";
import qs from "qs";
import { apiPrefix } from "../config";
// import { Message, Alert } from "element-ui"; // TODO

axios.defaults.baseURL = apiPrefix;

axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";

axios.interceptors.request.use(
  config => {
    // 
    if (config.method === "get") {
      let isQuery = /\?.*=.*/g.test(config.url);
      let timeStamp = new Date().getTime();
      if (isQuery) {
        config.url += `&t=${timeStamp}`;
      } else {
        config.url += `?t=${timeStamp}`;
      }
    }

    //  || config.method === "delete"
    if (config.method === "post" || config.method === "put") {
      config.data = qs.stringify(config.data);
    }
    return config;
  },
  error => {
    // Message({ // TODO
    //   showClose: true,
    //   message: error,
    //   type: "error"
    // })
    return Promise.reject(error);
})

/**
 *  response
 * 1.  http   200-400 
 * 2.  http   200-400   401 
 * 2.1   401 
 * 2.2   401 
 * 2.3 401
 */
axios.interceptors.response.use(
  res => {
    let { data } = res;
    return data;
  },
  error => {
    let { response } = error; // TODO

    if (!response) {
      return Promise.reject(error);
    }
    // let {data, status} = response;
    // Message({
    //   duration: 0,
    //   showClose: true,
    //   message: data.errors.message,
    //   type: "error"
    // })
    return Promise.reject(error);
  }
);

// Vue.http = Vue.prototype.$http = axios

export default function request({ method = "get", url, data, params }) {
  return axios.request({
    method: method,
    url,
    data,
    params,
    timeout: 20000
  });
}
